import { message } from 'antd';
import axios from '../axios';

import { REGISTER_REQUEST, REGISTER_SUCCESS, REGISTER_FAILURE } from './actionTypes';

export const registerRequest = () => {
  return {
    type: REGISTER_REQUEST,
  };
};

export const registerSuccess = user => {
  return {
    type: REGISTER_SUCCESS,
    user,
  };
};

export const registerFailure = () => {
  return {
    type: REGISTER_FAILURE,
  };
};

export const register = (values, { setErrors }) => async dispatch => {
  dispatch(registerRequest());
  if (navigator.onLine) {
    try {
      const response = await axios.post('/users/', { user: values });
      if (response.status === 200) {
        const user = await response.data.user;
        const token = await user.token;
        localStorage.setItem('token', token);
        dispatch(registerSuccess(user));
      }
    } catch (error) {
      dispatch(registerFailure());
      if (error.response.status === 404) {
        message.error('Not found requests');
      } else if (error.response.status === 403) {
        message.error('Forbidden requests');
      } else if (error.response.status === 401) {
        message.error('Unauthorized requests');
      } else if (error.response.status === 422) {
        const { errors } = await error.response.data;
        console.log(errors);
        const errorMessages = Object.keys(errors).map(name => {
          const msg = errors[name].join(' ');
          return { [`${name}`]: msg };
        });
        console.log({ ...errorMessages });

        // setErrors();
      } else {
        message.error('Something went wrong');
      }
    }
  } else {
    dispatch(registerFailure());
    message.error('Please check your network connectivity');
  }
};
